  height: 50px;
}

.img-40 {
  // width: 40px;
  height: 40px;
}

.img-25 {
  // width: 25px;
  height: 25px;
}

.img-claire {
  filter: invert(100%) grayscale(100%);
}

.img-change {
  filter: invert(0%);
  transition: filter 0.5s ease-in-out;
}

.img-change:hover {
  filter: invert(100%);
}

.img-zoom {
  animation: scale-down-center 1s ease-in-out both;
}

.img-zoom:hover {
  animation: scale-up-center 1s ease-in-out both;
}
.icone {
  border: solid 1px darkgrey;
}
.icone:hover {
  cursor: pointer;
  box-shadow: 3px 3px 6px black;
  background: lightgrey;
}

