/**
 * ----------------------------------------
 * animation scale-up-center
 * ----------------------------------------
 */
@keyframes scale-up-center {
  0% {
            transform: scale(1);
  }
  100% {
            transform: scale(1.1);
  }
}

@keyframes scale-down-center {
  0% {
            transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 992px) {
  .img-50 {
    height: 40px;
  }
  .img-100-100 {
    height: 50px;
  }

  .img-100 {
    display: none;
  }

  .img-90 {
    display:  none;
  }
}
